import { Record, List } from 'immutable';
import cuid from 'cuid';
import ValidationErrorDefinition from '../ValidationErrorDefinition';

const LogicalVariableDefinitionRecord = Record({
  _id: null,
  variableName: '',           // 
  operands: List(),           // outputDefinitionID
  operators: List(),          // +,-,*,/
});

/**  */
export default class LogicalVariableDefinition extends LogicalVariableDefinitionRecord {
  static create() {
    const id = cuid();
    const operands = List().push('').push('');
    const operators = List().push('');
    return new LogicalVariableDefinition({ _id: id, operands, operators });
  }

  getId() {
    return this.get('_id');
  }

  getVariableName() {
    return this.get('variableName');
  }

  getOperators() {
    return this.get('operators');
  }

  getOperands() {
    return this.get('operands');
  }

  /**  */
  createFunctionCode(survey, page) {
    const node = survey.findNodeFromRefId(page.getId());
    // OutputDefinition
    const precedingOutputDefinition = survey.findPrecedingOutputDefinition(node.getId(), false, false);
    // OutputDefinition
    const allOutputDefinitionMap = survey.getAllOutputDefinitionMap();
    let code = 'return ';
    const operands = this.getOperands();
    const operators = this.getOperators();
  
    operands.forEach((operand, i) => {
      const outputDefinition = precedingOutputDefinition.find(od => od.getId() === operand);
      if (i > 0) {
        code += `${operators.get(i - 1)}`;
      }
      if (outputDefinition === undefined) {
        // 
        // answers
        // 
        code += `(parseInt(($('*[name="${allOutputDefinitionMap.get(operand).getName()}"]:enabled:visible').val() || '').replace(/[-]/g, function(ch) { return String.fromCharCode(ch.charCodeAt(0) - 65248); }), 10) || 0)`;
      } else {
        // answers
        code += `(parseInt(answers['${outputDefinition.getName()}'], 10) || 0)`;
      }
    });
    return code;
  }

  /**  */
  validate(survey, page) {
    let errors = List();
    if (this.getOperators().findIndex(ope => ope === '') !== -1) {
      errors = errors.push(ValidationErrorDefinition.createError(`${this.getVariableName()}`));
    }
    const currentNode = survey.findNodeFromRefId(page.getId());
    const precedingOutputDefinitions = survey.findPrecedingOutputDefinition(currentNode.getId());
    if (!this.getOperands().every(opr => !!precedingOutputDefinitions.find(od => od.getId() === opr))) {
      errors = errors.push(ValidationErrorDefinition.createError(`${this.getVariableName()}`));
    }
    return errors;
  }
}



