import S from 'string';
import { List } from 'immutable';

export default class Replacer {
  /** {{cj14nvvqj001w3k67ycbtorpn.answer}}  OutputDefinition.id cj14nvvqj001w3k67ycbtorpn  */
  static extractIdFrom(str) {
    const regexp = /{{([^.]+)\./;
    const m = regexp.exec(str);
    if (m === null) return null;
    return m[1];
  }

  /** {{cj14nvvqj001w3k67ycbtorpn.answer}}   answer  */
  static extractReferenceType(str) {
    const regexp = /{{([^.]+)\.(.+?)}}/;
    const m = regexp.exec(str);
    if (m === null) return null;
    return m[2];
  }

  static convert(str, dictionary) {
    if (typeof str !== 'string') return str;
    let result = str;
    for (const key in dictionary) {
      if (!Object.prototype.hasOwnProperty.call(dictionary, key)) continue;
      for (;;) {
        // 
        // dictionary
        // 
        const old = result;
        result = result.replace(key, dictionary[key]);
        if (result === old) break;
      }
    }
    return result;
  }

  static createId2NoParams(allOutputDefinitionMap, reverse) {
    const params = {};
    allOutputDefinitionMap.forEach((od) => {
      const key1 = `{{${od.getId()}.answer}}`;
      const key2 = `{{${od.getId()}.answer_label}}`;
      const value1 = `{{${od.getOutputNo()}.answer}}`;
      const value2 = `{{${od.getOutputNo()}.answer_label}}`;
      if (reverse) {
        params[value1] = key1;
        params[value2] = key2;
      } else {
        params[key1] = value1;
        params[key2] = value2;
      }
    });
    return params;
  }

  static createId2NameParams(allOutputDefinitionMap) {
    const params = {};
    allOutputDefinitionMap.forEach((od) => {
      params[`{{${od.getId()}.answer}}`] = `{{${od.getName()}}.answer}}`;
    });
    return params;
  }

  static createId2ValueParams(allOutputDefinitionMap, answers) {
    const params = {};
    allOutputDefinitionMap.forEach((od) => {
      const answerValue = answers[od.getName()] === undefined ? '' : answers[od.getName()];
      params[`{{${od.getId()}.answer}}`] = answerValue;
      const type = od.getOutputType();
      if (type === 'checkbox') {
        params[`{{${od.getId()}.answer_label}}`] = od.getLabel();
      } else if (od.isOutputTypeSingleChoice()) {
        const selectedChoice = od.getChoices().find(choice => choice.getValue() === answerValue);
        od.getChoices().forEach((choice) => {
          // choice
          params[`{{${choice.getId()}.choice_value}}`] = choice.getValue();
        });
        params[`{{${od.getId()}.answer_label}}`] = selectedChoice ? selectedChoice.getLabel() : '';
      }
    });
    return params;
  }

  static createId2SpanParams(allOutputDefinitionMap) {
    const params = {};
    allOutputDefinitionMap.forEach((od) => {
      params[`{{${od.getId()}.answer}}`] = `<span class="answer-value" data-reprint="${od.getId()}"> ${od.getOutputNo()}</span>`;
      const type = od.getOutputType();
      if (type === 'checkbox' || od.isOutputTypeSingleChoice()) {
        // .answercheckbox, radio, select.answer_labelanswer_label
        params[`{{${od.getId()}.answer_label}}`] = `<span class="answer-value" data-reprint="${od.getId()}"> ${od.getOutputNo()}</span>`;
      }
    });
    return params;
  }

  /**
   * 
   *
   * @param {Object} allOutputDefinitionMap 
   * @param {Object} questionNoMap Questionoutputname
   * @param {Object} answers answer
   */
  constructor(allOutputDefinitionMap, answers) {
    this.allOutputDefinitionMap = allOutputDefinitionMap;
    this.answers = answers;

    this.id2NoParams = Replacer.createId2NoParams(allOutputDefinitionMap, false);
    this.no2IdParams = Replacer.createId2NoParams(allOutputDefinitionMap, true);
    this.id2NameParams = Replacer.createId2NameParams(allOutputDefinitionMap);
    this.id2ValueParams = Replacer.createId2ValueParams(allOutputDefinitionMap, answers);
    this.id2SpanParams = Replacer.createId2SpanParams(allOutputDefinitionMap, answers);
  }

  /**
   * 
   *
   * 
   * {{<outputId>.<answer|answer_label|choice_value>}}
   * :
   *   {{cizxhx0wb00013j5z5kq89wii.answer}} => 
   *   {{cizxhx0wb00013j5z5kq89wii.answer_label}} => (radio, select)
   *   {{cizxhx0wb00013j5z5kq89wii.choice_value}} => Choice()
   *
   * 
   * Question
   *   {{1-1-1.answer}}
   * survey
   *   {{cizxhx0wb00013j5z5kq89wii.answer}}
   * 
   *   
   *  {{cizxhx0wb00013j5z5kq89wii.answer}}   
   */
  id2Value(str) {
    return Replacer.convert(str, this.id2ValueParams);
  }

  /**
   * idspan
   */
  id2Span(str) {
    return Replacer.convert(str, this.id2SpanParams);
  }

  /** idName */
  id2Name(str) {
    return Replacer.convert(str, this.id2NameParams);
  }

  /**
   * 
   *
   * 
   * {{1-1-1.answer}}{{cizxomo8f00033j5zz2nt9kvg.answer}}
   */
  no2Id(str) {
    return Replacer.convert(str, this.no2IdParams);
  }

  /**
   * no2Id
   */
  id2No(str) {
    return Replacer.convert(str, this.id2NoParams);
  }

  /** id */
  containsReferenceIdIn(str) {
    return this.id2No(str) !== str;
  }

  /**
   * strOutputDefinition
   */
  findReferenceOutputDefinitionsIn(str) {
    if (!this.containsReferenceIdIn(str)) return [];
    const outputDefinitions = [];
    let result = str;
    for (const key in this.id2NoParams) {
      if (!Object.prototype.hasOwnProperty.call(this.id2NoParams, key)) continue;
      const old = result;
      result = result.replace(key, this.id2NoParams[key]);
      if (result === old) continue;
      const id = Replacer.extractIdFrom(key);
      outputDefinitions.push(this.allOutputDefinitionMap.get(id));
    }
    return outputDefinitions;
  }

  /** {{cizxomo8f00033j5zz2nt9kvg.answer}} */
  findReferenceLabelIn(str) {
    const id = Replacer.extractIdFrom(str);
    const referenceType = Replacer.extractReferenceType(str);
    // choice_valuechoice
    if (referenceType === 'choice_value') {
      const allOutputDefinitionList = this.allOutputDefinitionMap.toList();
      for (let i = 0; i < allOutputDefinitionList.size; i++) {
        const od = allOutputDefinitionList.get(i);
        const choices = od.getChoices() || List();
        const choiceIndex = choices.findIndex(choice => choice.getId() === id);
        if (choiceIndex !== -1) {
          return od.getChoiceLabelWithOutputNo(choiceIndex);
        }
      }
    }
    // outputDefinition
    const targetOutputDefinition = this.allOutputDefinitionMap.get(id);
    if (targetOutputDefinition) {
      return `${targetOutputDefinition.getLabelWithOutputNo()}`;
    }

    return null;
  }

  /**  */
  validate(str, outputDefinitions) {
    const regexp = /{{([^.]+)\.(answer|answer_label|choice_value)}}/g;
    let m;
    for (;;) {
      m = regexp.exec(str);
      if (m === null) break;
      // 
      const key = `{{${m[1]}.${m[2]}}}`;
      if (!(key in this.id2ValueParams)) {
        return false;
      }
      // outputDefinition
      if (m[2] === 'answer' || m[2] === 'answer_label') {
        const targetOutputDefinition = outputDefinitions.find(od => od.getId() === m[1]);
        if (targetOutputDefinition === undefined) return false;
      } else if (m[2] === 'choice_value') {
      // choice_valuechoice
        const targetChoice = outputDefinitions.flatMap(od => od.getChoices()).find(choice => choice.getId() === m[1]);
        if (targetChoice === undefined) return false;
      }
    }
    return true;
  }
}



