import cuid from 'cuid';
import { Record, List } from 'immutable';
import PersonalInfoItemDisplayTypeDefinition from './PersonalInfoItemDisplayTypeDefinition';
import * as FIELDS from '../../../../../constants/personalInfoFields';

export const PersonalInfoItemRecord = Record({
  _id: null,                                  // ID
  dataType: 'PersonalInfoItem',               // Item
  devId: null,                                // DevId
  index: -1,                                  // 
  label: '',                                  // HTML
  plainLabel: '',                             // TEXT
  value: '',                                  //  (Item)
  rowType: null,                              // 
  isOptional: false,                          //  true false
  displayTypeId: null,                        // 
});

export default class PersonalInfoItemDefinition extends PersonalInfoItemRecord {
  static create(index, rowType, label, isOptional, displayTypeId) {
    return new PersonalInfoItemDefinition({
      _id: cuid(),
      index,
      rowType,
      isOptional,
      displayTypeId,
      label,
      plainLabel: label,
    });
  }

  getId() {
    return this.get('_id');
  }

  getIndex() {
    return this.get('index');
  }

  getLabel() {
    return this.get('label');
  }

  getPlainLabel() {
    return this.get('plainLabel');
  }

  getDevId() {
    return this.get('devId');
  }

  getRowType() {
    return this.get('rowType');
  }

  getFields() {
    const currentDisplayTypeId = this.getDisplayTypeId();
    const displayType = this.getDisplayTypeCandidates().find(dt => dt.getId() === currentDisplayTypeId);
    return displayType ? displayType.getPersonalItemFields() : List();
  }

  getDisplayTypeId() {
    return this.get('displayTypeId');
  }

  getDisplayTypeCandidates() {
    let displayTypeCandidates;
    switch (this.getRowType()) {
      case 'AgeRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.age, label: '', outputType: 'number' },
            ],
          },
        ];
        break;
      case 'BirthYearRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.birthYear, label: '', outputType: 'number' },
            ],
          },
        ];
        break;
      case 'ContactEasyTimeRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: 'input',
            label: '1',
            fields: [
              { id: FIELDS.contactEasyTime, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'ContactMeansRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: 'checkbox',
            label: '',
            fields: [
              { id: FIELDS.contactMeans, label: '', outputType: 'checkbox' },
            ],
          },
        ];
        break;
      case 'EmailRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.email, label: 'E', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'FaxRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.fax, label: 'FAX', outputType: 'text', prependValue: '`' },
            ],
          },
          {
            id: '3input',
            label: '3',
            fields: [
              { id: FIELDS.fax1, label: 'FAX 1', outputType: 'text', prependValue: '`' },
              { id: FIELDS.fax2, label: 'FAX 2', outputType: 'text', prependValue: '`' },
              { id: FIELDS.fax3, label: 'FAX 3', outputType: 'text', prependValue: '`' },
            ],
          },
        ];
        break;
      case 'FuriganaRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.furigana, label: '', outputType: 'text' },
            ],
          },
          {
            id: '2input',
            label: '2',
            fields: [
              { id: FIELDS.furiganaFirst, label: '()', outputType: 'text' },
              { id: FIELDS.furiganaLast, label: '()', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'NameRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.name, label: '', outputType: 'text' },
            ],
          },
          {
            id: '2input',
            label: '2',
            fields: [
              { id: FIELDS.lastName, label: '()', outputType: 'text' },
              { id: FIELDS.firstName, label: '()', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'HomeTelRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.homeTel, label: 'TEL', outputType: 'text', prependValue: '`' },
            ],
          },
          {
            id: '3input',
            label: '3',
            fields: [
              { id: FIELDS.homeTel1, label: 'TEL 1', outputType: 'text', prependValue: '`' },
              { id: FIELDS.homeTel2, label: 'TEL 2', outputType: 'text', prependValue: '`' },
              { id: FIELDS.homeTel3, label: 'TEL 3', outputType: 'text', prependValue: '`' },
            ],
          },
        ];
        break;
      case 'HospitalNameRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.hospitalName, label: '()', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'HospitalPrefectureRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: 'select',
            label: '',
            fields: [
              { id: FIELDS.hospitalPrefecture, label: '()', outputType: 'text' },
            ],
          },
          { id: 'select_input',
            label: '+',
            fields: [
              { id: FIELDS.hospitalPrefecture, label: '()', outputType: 'text' },
              { id: FIELDS.hospitalPrefectureText, label: '()', outputType: 'text' },
            ],
          },
          {
            id: 'input',
            label: '',
            fields: [
              { id: FIELDS.hospitalPrefectureText, label: '()', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'InterviewMeansRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: 'radio',
            label: '',
            fields: [
              { id: FIELDS.interviewMeans, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'InterviewPlaceRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: 'input',
            label: '1',
            fields: [
              { id: FIELDS.InterviewPlace, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'InterviewRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: 'multi_input',
            label: '1',
            fields: [
              { id: FIELDS.interviewContactMobileTel, label: ':', outputType: 'checkbox' },
              { id: FIELDS.interviewContactHomeTel, label: ':TEL', outputType: 'checkbox' },
              { id: FIELDS.interviewContactWorkTel, label: ':TEL', outputType: 'checkbox' },
            ],
          },
        ];
        break;
      case 'MobileTelRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.mobileTel, label: '', outputType: 'text', prependValue: '`' },
            ],
          },
          {
            id: '3input',
            label: '3',
            fields: [
              { id: FIELDS.mobileTel1, label: ' 1', outputType: 'text', prependValue: '`' },
              { id: FIELDS.mobileTel2, label: ' 2', outputType: 'text', prependValue: '`' },
              { id: FIELDS.mobileTel3, label: ' 3', outputType: 'text', prependValue: '`' },
            ],
          },
        ];
        break;
      case 'PositionRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.position, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'ProfessionalAreaRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.professionalArea, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'RequestEtcRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.requestEtc, label: '', outputType: 'text', prependValue: '`' },
            ],
          },
        ];
        break;
      case 'ScheduleRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: 'multi_input',
            label: '1',
            fields: [
              { id: FIELDS.scheduleContactMobileTel, label: ':', outputType: 'checkbox' },
              { id: FIELDS.scheduleContactHomeTel, label: ':TEL', outputType: 'checkbox' },
              { id: FIELDS.scheduleContactWorkTel, label: ':TEL', outputType: 'checkbox' },
              { id: FIELDS.scheduleContactEmail, label: ':E', outputType: 'checkbox' },
              { id: FIELDS.contactTimeWeekday, label: ':', outputType: 'text' },
              { id: FIELDS.contactTimeWeekend, label: ':', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'SexRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.sex, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'SpecialityRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.specialty, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'WorkPostalCodeRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.workPostalCode, label: '', outputType: 'text' },
            ],
          },
        ];
        break;
      case 'WorkTelRow':
        displayTypeCandidates = [
          { id: 'none', label: '' },
          {
            id: '1input',
            label: '1',
            fields: [
              { id: FIELDS.workTel, label: 'TEL', outputType: 'text', prependValue: '`' },
            ],
          },
          {
            id: '3input',
            label: '3',
            fields: [
              { id: FIELDS.workTel1, label: 'TEL 1', outputType: 'text', prependValue: '`' },
              { id: FIELDS.workTel2, label: 'TEL 2', outputType: 'text', prependValue: '`' },
              { id: FIELDS.workTel3, label: 'TEL 3', outputType: 'text', prependValue: '`' },
            ],
          },
        ];
        break;
      default:
        throw new Error(`RowTypeoutputType: ${this.getRowType()}`);
    }

    return List(displayTypeCandidates.map(displayType => PersonalInfoItemDisplayTypeDefinition.create(displayType)));
  }

  isOptional() {
    return this.get('isOptional');
  }

  isHomeTelRow() {
    return this.getRowType() === 'HomeTelRow';
  }

  isMobileTelRow() {
    return this.getRowType() === 'MobileTelRow';
  }

  isWorkTelRow() {
    return this.getRowType() === 'WorkTelRow';
  }

  isEmailRow() {
    return this.getRowType() === 'EmailRow';
  }

  isDisable() {
    return this.getDisplayTypeId() === 'none';
  }
}



