import cuid from 'cuid';
import { List } from 'immutable';
import RadioQuestionDefinition from './RadioQuestionDefinition';
import ItemDefinition from './internal/ItemDefinition';
import surveyIdGeneratorInstance from '../../../SurveyDevIdGenerator';

/**  */
export default class ScreeningAgreementQuestionDefinition extends RadioQuestionDefinition {
  static create(pageDevId) {
    const questionDevId = surveyIdGeneratorInstance.generateForQuestion(pageDevId);
    const items = List([
      '',
      '',
      '',
    ]).map((label, index) => {
      const itemDevId = surveyIdGeneratorInstance.generateForItem(questionDevId);
      return ItemDefinition.create(itemDevId, index)
        .set('label', label)
        .set('plainLabel', label)
        .set('value', `${index + 1}`);
    }).toList();
    return new ScreeningAgreementQuestionDefinition({
      _id: cuid(),
      devId: questionDevId,
      dataType: 'ScreeningAgreement',
      title: '',
      plainTitle: '',
      description: `
        <div class="agreement-description">
          <p></p>
          <div class="informationBox">
             <br>
            <u></u><br>
            <br>
            <br>
             <br>
            <br>
                   <br>
             <br>
            <u></u>
            <br>
            1) <br>
            2) <br>
            3) <br>
            
          </div>
          <p></p>
        </div>`,
      items,
    });
  }
}



