import { Record } from 'immutable';

/** 1 */
export const OutputDefinitionRecord = Record({
  _id: null,           // IDitemnode
  devId: null,         // id(JavaScript)
  name: null,          // post
  label: null,         // 
  dlLabel: null,       // DL
  questionId: null,    // questionId  ####question
  question: null,      // question    ####
  outputType: null,    // checkbox, radio, text, number
  choices: null,       // 
  outputNo: null,      // outputDefition
  downloadable: true,  // DL
  prependValue: null,  // DL
});

export default class OutputDefinition extends OutputDefinitionRecord {
  getId() {
    return this.get('_id');
  }

  getDevId() {
    return this.get('devId');
  }

  getName() {
    return this.get('name');
  }

  getLabel() {
    return this.get('label');
  }

  getDlLabel() {
    return this.get('dlLabel');
  }

  getOutputType() {
    return this.get('outputType');
  }

  getChoices() {
    return this.get('choices');
  }

  getPageId() {
    return this.get('pageId');
  }

  getQuestion() {
    return this.get('question');
  }

  getOutputNo() {
    return this.get('outputNo');
  }

  /** outputNolabel. : 1-1-1 1 */
  getLabelWithOutputNo() {
    return `${this.getOutputNo()} ${this.getLabel()}`;
  }

  /** outputNochoicelabel. : 1-1(1) 1 */
  getChoiceLabelWithOutputNo(choiceIndex) {
    return `${this.getOutputNo()}(${choiceIndex + 1}) ${this.getChoices().get(choiceIndex).getLabel()}`;
  }

  getPrependValue() {
    return this.get('prependValue');
  }

  // option
  getLabelForCondition(natural = false) {
    const label = this.getLabelWithOutputNo();
    if (!natural) return label;
    const outputType = this.getOutputType();
    switch (outputType) {
      case 'checkbox':
        return label;
      case 'radio':
      case 'select':
      case 'text':
      case 'number':
        return `${label} `;
      default:
        throw new Error(`outputType: ${outputType}`);
    }
  }

  /** outputType */
  isOutputTypeSingleChoice() {
    switch (this.getOutputType()) {
      case 'select':
      case 'radio':
        return true;
      default:
        return false;
    }
  }

  /** OutputDefinition */
  isLogicalVariableOutputDefinition() {
    return this.getOutputNo().split('-')[1] === 'L';
  }
}



